import Slider from "../Components/Slider";
// import Rating from "../Components/Rating";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay } from "swiper";
import { Navigation } from "swiper";
import { Pagination } from "swiper";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import RatedBlog from "./RatedBlog";
import Faq from "react-faq-component";
import { useState, useEffect } from "react";
import { NavLink, Link, useParams } from "react-router-dom";
function Homepage() {
  let { id } = useParams();
  const data = {
    rows: [
      {
        title: "Bizim hans xidmtlrimiz var?",
        content:
          "Neft v qaz kfiyyat xidmtlri </br> Geoloji kfiyyat v tdqiqat </br> Da-mdn tdqiqatlar </br> traf mhitin v thlknin qiymtlndirilmsi, xsusil kimyvi irklnm, tullant anbarlarnda riskin qiymtlndirilmsi, LNAPL irklnmsinin srhdlrinin myyn edilmsi v srmlrin, uxurlarn v s. v thlknin qiymtlndirilmsi </br> Tunel v bnd v s. infrastrukturlar </br> Qrunt sualrnn kfiyyat </br> Geofiziki xidmtlr (seysmik tdqiqat, maqnit tdqiqat, qravitasiya tdqiqat mqavimti, induksiya edilmi qtblm v spektral sbbli qtblm tdqiqatlar",
    
      
      },
      {
        title: "Bu xidmtlr hasn region v blglrd hyata keirilir?",
        content: `Artq illrdiki z kefyiyyti il faliyyt gstrn AzGP istr lkdaxili istrs d lkxarici faliyytlri il xidmtinizddir. `,
      },
      {
        title: "Niy biz?",
        content:
          "Xidmtinin keyfiyyti il sizlrin gvnini qazanan AzGP irkti z tcrbsi v pekarl il sahsind z szn deyir.",
      },
    ],
  };
  const [rated, setRated] = useState([]);
  useEffect(() => {
    const getData = async () => {
      let data = await fetch("https://admin.azgpen.com/contents").then((a) =>
        a.json()
      );

      setRated(data);
    };
    getData();
  }, []);

  //industry ve blogs istifade etmisem
  const [industry, setIndustry] = useState([]);
  useEffect(() => {
    const getInfo = async () => {
      let data = await fetch("https://admin.azgpen.com/blog/GalleryView/").then(
        (a) => a.json()
      );
      setIndustry(data);
    };
    getInfo();
  }, []);
 
  const [blogs, setBlogs] = useState([]);
  useEffect(() => {
    const getInfo = async () => {
      let data = await fetch("https://admin.azgpen.com/blog/NewsView/").then((a) =>
        a.json()
      );
      setBlogs(data);
    };
    getInfo();
  }, []);

  return (
    <>
      <Slider />

      <div className="navshops">
        <div className="container">
          <Swiper
            breakpoints={{
              120: {
                slidesPerView: 1,
              },
              320: {
                slidesPerView: 1,
              },
              640: {
                slidesPerView: 2,
              },
              768: {
                slidesPerView: 3,
              },
            }}
            loop="true"
            autoplay={{
              delay: 2500,
            }}
            modules={[Autoplay, Navigation]}
            navigation={true}
            slidesPerView={3}
          >
            {" "}
            {industry.map((a) => (
              <SwiperSlide>
                <div className="navshop">
                  <div className="navshop-image">
                    <img src={a.image} />
                  </div>
                  <div className="navshop-details">
                    <p>{a.name}</p>
                  </div>
                </div>
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
      </div>

      <div className="organic">
        <div className="organic-image">
          <i className="fa-solid fa-industry"></i>
        </div>
        <div className="organic-text">
          <p>Biz Aparc Mhndislik Snayesiyik</p>
        </div>
      </div>

      <RatedBlog />
      <div className="container">
        <div className="headingg">
          <h1>Trfdalar</h1>
        </div>
        <div className="navshopps">
          <Swiper
            breakpoints={{
              120: {
                slidesPerView: 1,
              },
              320: {
                slidesPerView: 2,
              },
              640: {
                slidesPerView: 2,
              },
              768: {
                slidesPerView: 3,
              },
            }}
            loop="true"
            autoplay={{
              delay: 2500,
            }}
            modules={[Autoplay, Pagination]}
            pagination={{
              clickable: true,
              
            }}
            slidesPerView={3}
          >
            {" "}
            {blogs.map((blog) => (
              <SwiperSlide>
                {" "}
                <div className="navshopp">
                  <div className="navshopp-image">
                    <img src={blog.image} />
                  </div>
                  <div className="navshop-details">
                    <div className="navshop-toptext">
                      <p>{blog.name}</p>
                    </div>
                  </div>
                </div>
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
      </div>
      <div className="heading">
        <h1>Tez-tez veriln suallar</h1>
      </div>
      <div className="container">
        <div className="faq">
          <Faq data={data} />
        </div>
      </div>
    </>
  );
}
export default Homepage;
